<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>物资储备</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }">物资列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input v-model="goodsName" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="this.getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="searchGoods"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="addVisible=true">添加数据</el-button>
          <el-button type="danger">批量删除</el-button>
        </el-col>
      </el-row>
      <el-table
          :cell-style="rowClass"
          :data="goodsList"
          :header-cell-style="headClass"
          border
          style="width: 100%"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
            label="序号"
            type="index"
            width="45px">
        </el-table-column>
        <el-table-column
            label="商品名称"
            prop="title"
            width="150px">
        </el-table-column>
        <el-table-column
            :show-overflow-tooltip="true"
            label="描述"
            prop="describes"
        >
        </el-table-column>

        <el-table-column label="图片">
          <template slot-scope="scope">
            <el-popover placement="top-start" title="" trigger="hover">
              <img :src="scope.row.image" alt="" style="width: 200px;height: 200px">
              <img slot="reference" :src="scope.row.image" style="width: 30px;height: 30px">
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column
            label="交易数量"
            prop="count"
            width="100px">
        </el-table-column>
        <el-table-column
            label="剩余数量"
            prop="delCount"
            width="100px">
        </el-table-column>
        <el-table-column
            label="操作"
            width="150px"
        >
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary" @click="editGood(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start"
                        @click="deleteGood(scope.row.goodsId)">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="deleteGood(scope.row.goodsId)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="this.pageNum"
          :page-size="this.pageSize"
          :page-sizes="[5,8,10,12,15]"
          :total="this.total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>

    <!--增加对话框-->
    <el-dialog
        :visible.sync="addVisible"
        title="增加数据"
        width="35%"
        @close="closeAddDialog"
    >
      <!-- 这是内容区域-->
      <el-form ref="addFormRef" v-model="Good" class="demo-Good" label-width="100px">
        <el-form-item label="商品名称" prop="title">
          <el-input v-model="Good.title"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="describes">
          <el-input v-model="Good.describes"></el-input>
        </el-form-item>
        <el-form-item label="交易数量" prop="count">
          <el-input v-model="Good.count"></el-input>
        </el-form-item>
        <el-form-item label="剩余数量" prop="delCount">
          <el-input v-model="Good.delCount"></el-input>
        </el-form-item>
        <el-form-item label="选择图片">
          <el-upload :auto-upload="false" :limit="1" :on-change="handListChange" action="" list-type="picture-card"
                     style="float:left">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <el-button @click="addVisible = false">取 消</el-button>
      <el-button type="primary" @click="confirmAdd">确 定</el-button>
    </el-dialog>

    <!--修改对话框-->
    <el-dialog
        :visible.sync="editVisible"
        title="修改数据"
        width="35%"
        @close="closeAddDialog"
    >
      <!-- 这是内容区域-->
      <el-form ref="addFormRef" :model="Good" class="demo-Good" label-width="100px">
        <el-form-item label="商品名称" prop="title">
          <el-input v-model="Good.title"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="describes">
          <el-input v-model="Good.describes"></el-input>
        </el-form-item>
        <el-form-item label="交易数量" prop="count">
          <el-input v-model.number="Good.count"></el-input>
        </el-form-item>
        <el-form-item label="剩余数量" prop="delCount">
          <el-input v-model.number="Good.delCount"></el-input>
        </el-form-item>
        <el-form-item label="图片">
          <img :src="Good.image" alt="" style="float:left; width: 200px;height: 200px">
        </el-form-item>
      </el-form>
      <el-button @click="editVisible = false">取 消</el-button>
      <el-button type="primary" @click="confirmUpdate">确 定</el-button>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: "Goods",
  data() {
    return {
      addVisible: false,
      editVisible: false,
      goodsName: '',
      pageNum: 1,
      pageSize: 5,
      total: null,
      goodsList: [],
      Good: {
        count: '',
        delCount: null,
        describes: '',
        title: ''
      },
      file: '',
    }
  },
  methods: {
    removeUserItem(row) {
    },
    handleSelectionChange(val) {
    },
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    getGoodsList() {
      this.$axios.get('/goods/getAllPageGoods', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }
      }).then(res => {
        const resp = res.data.data;
        this.total = resp.total;
        this.goodsList = resp.list;
      })
    },
    handleSizeChange(newSize) {
      this.pageSize = newSize;
      this.getGoodsList();
    },
    handleCurrentChange(newNum) {
      this.pageNum = newNum;
      this.getGoodsList();
    },
    closeAddDialog() {
      this.$refs["addFormRef"].resetFields();
    },
    //上传图片
    handListChange(file) {
      let formdata = new FormData()
      formdata.append("file", file.raw)
      this.file = formdata;
    },
    searchGoods() {
      // this.$axios.get('/')
    },
    //确定添加
    confirmAdd() {
      this.$axios.post('/goods/oosFile/upload', this.file).then(res => {
        // console.log(res)
        this.file = '';
        if (res.data.status === 200) {
          this.Good.image = res.data.data
          console.log(this.Good)
          this.$axios.post('/goods/add', this.Good).then(res => {
            this.$message.success("添加成功！")
          })
        }
      })
      this.addVisible = false;
    },
    editGood(raw) {
      console.log(raw, '修改商品');
      this.Good = raw;
      this.editVisible = true;
    },
    confirmUpdate() {
      this.$axios.post('/goods/updateByGoodsId', this.Good).then(res => {
        if (res.data.status === 10000) {
          this.$message.success("修改成功！")
        }
      })
      this.editVisible = false;
    },
    closeEditDialog() {
      //重置表单
      this.$refs["addFormRef"].resetFields();
    },
    deleteGood(goodsId) {
      this.$confirm('此操作将永久删除该商品, 是否继续?', '删除商品', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.get('/goods/deleteByGoodsId', {params: {goodsId: goodsId}}).then(res => {
          if (res.data.status === 10000) {
            this.$message.success("删除成功！")
            this.getGoodsList();
          } else {
            this.$message.error("删除失败")
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
  },
  created() {
    this.getGoodsList();
  }
}
</script>

<style scoped>

</style>
